export default {
	methods: {
		logout() {

			uni.showModal({
				title: '提示',
				content: '确认退出登陆？',
				success: function(res) {
					if (res.confirm) {
						uni.removeStorage({
							key: 'token',
							success(res) {
								uni.redirectTo({
									url: '/pages/login/login'
								})
							}
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});


		}
	}
}
